import React, { Component } from 'react';

import { Link } from 'react-router-dom';
import Layout from '../components/Layout';
import BgImage from "../assets/img/illustrations/signin.svg";



export default class Signup extends Component {
  static displayName = Signup.name;

  render () {
  return (
      <section className="d-flex align-items-center my-5 mt-lg-6 mb-lg-5">
        <div className='container'>
          <p className="text-center">
            <Link to='/' className="card-link text-gray-700">
            <i className="bi bi-chevron-double-right mt-2"></i> Back to homepage
            </Link>
          </p>
          <div className="row justify-content-center form-bg-image" style={{ backgroundImage: `url(${BgImage})` }}>
            <div className="col-xs-12 d-flex align-items-center justify-content-center">
              <div className="mb-4 mb-lg-0 bg-white shadow-soft border rounded border-light p-4 p-lg-5 w-100 fmxw-500">
                <div className="text-center text-md-center mb-4 mt-md-0">
                  <h3 className="mb-0">Create an account</h3>
                </div>
                <form className="mt-4">
                  <div id="email" className="input-group mb-4">
                    <label className='input-group-text'>Your Email</label>
                    <div className='input-group'>
                      <div className='input-group-prepend'>
                      <i className="bi bi-envelop text-black"></i>
                      </div>
                      <input className='form-control' name="email" autoFocus required type="email" placeholder="example@company.com" />
                    </div>
                  </div>
                  <div id="password" className="input-group mb-4">
                    <label className='input-group-text'>Your Password</label>
                    <div className='input-group'>
                      <div className='input-group-prepend'>
                      <i className="bi bi-unlock text-success"></i>
                      </div>
                      <input className='form-control' name="password" required type="password" placeholder="Password" />
                    </div>
                  </div>
                  <div  id="confirmPassword" className=" input-group mb-4">
                    <label className='input-group-text'>Confirm Password</label>
                    <div className='input-group'>
                      <div className='input-group-prepend'>
                      <i className="bi bi-unlock text-dark "></i>
                      </div>
                      <input className='form-control' name="confirm-password" required type="password" placeholder="Confirm Password" />
                    </div>
                  </div>
                  <div className='input-group'>
          
                   <label  htmlFor="terms">
                   I agree to the <Link to='/' className='card-link'>terms and conditions</Link>
                   </label>
                  <input title='terms' type="checkbox" className='form-control' />
                  </div>
                  <button  type="submit" className="btn btn-primary btn-block">
                    Sign up
                  </button>
                </form>

                <div className="mt-3 mb-4 text-center">
                  <span className="fw-normal">or</span>
                </div>
                <div className="d-flex justify-content-center my-4">
                  <Link to='/'  className="btn btn-outline-primary  btn-pill btn-success me-2">
                  <i className="bi bi-facebook text-primary fs-2 text"></i>
                  </Link>
                  <Link to='/'  className="btn btn-outline-light  btn-pill btn-success me-2">
                  <i className="bi bi-twitter text-success fs-2 text"></i>
                  </Link>
                  <Link to='/' className="btn btn-outline-light-only btn-pil btn-dark">
                  <i className="bi bi-github text-secondary fs-2 text"></i>
                  </Link>
                </div>
                <div className="d-flex justify-content-center align-items-center mt-4">
                  <span className="fw-normal">
                    Already have an account?
                    <Link to='/login' className="card-link fw-bold">
                      {` Login here `}
                    </Link>
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
  );
};
}