import { Row, Col } from 'react-bootstrap';
import { FormBuilder } from '../utils/html5validation';
import MaroonCard from '../components/MaroonCard';

function Contactme() {


  return (

    <Row className="justify-content-md-center">
      <Col md="6" className="mx-auto">
        <MaroonCard title='Contact'>
        <h4 className='text-center'>Please use this form</h4>

          <hr className='py-2 bg-warning'/>

            {
              FormBuilder({
                elements: [
                  {
                    lable: "Select subject",
                    attrs: {
                      select: {
                        name: "subject",
                        required: true,
                        lable: "Select subject",
                        options: [
                          { title: "consultation", value: "1" },
                          { title: "Question", value: "2" },
                          { title: "Contact", value: "3" }
                        ]
                      }
                    }
                  },
                  {
                    lable: "Full Name",
                    attrs: {
                      input: {
                        name: "fullname",
                        type: "text",
                        required: true,
                        placeholder: "Your Name",
                        pattern: '^[A-Za-z0-9 _]*[A-Za-z0-9][A-Za-z0-9 _]',
                        minLength: 3,
                        maxLength: 30
                      }
                    }
                  },
                  {
                    lable: "Email Address",
                    attrs: {
                      input: {
                        name: "email",
                        type: "email",
                        required: true,
                        placeholder: "Your email",
                        minLength: 3,
                        maxLength: 30
                      }
                    }
                  },
                  {
                    lable: "Telphone Number",
                    attrs: {
                      input: {
                        name: "telphone",
                        type: "tel",
                        placeholder: "Your phone number",
                        pattern: '[+0-9]{11,15}',
                      }
                    }
                  },
                  {
                    lable: "Your Message",
                    attrs: {
                      input: {
                        name: "message",
                        required: true,
                        placeholder: "Your message",
                        as:"textarea",
                        minLength: 3,
                        style: { height: '100px' }
                      }
                    }
                  },
                  {
                    lable: "Shall we call you .?",
                    isCheckbox: true,
                    attrs: {
                      input: {
                        name: "terms",
                        type: "checkbox",
                        required: true,
                      }
                    }
                  }
                ],
                form: {
                  attrs: {
                    id: "contactForm",
                    name: "contactForm",
                  },
                  cleandata: (d: any) => alert(d)
                },
                addSubmit:true
              })
            }
         
        </MaroonCard>
      </Col>
    </Row>

  );
}
export default Contactme;