import react, { HTMLInputTypeAttribute, useState } from 'react';
import { Form, Button, FloatingLabel, InputGroup, FloatingLabelProps, InputGroupProps, FormGroupProps, FormControlProps, FormSelectProps, FormProps, FormCheckProps } from 'react-bootstrap';

const uuid = window.crypto.randomUUID; // This feature is available only in (HTTPS).output => "36b8f84d-df4e-4d49-b662-bcde71a8764f"
const getRandom = (num = 1) => window.crypto.getRandomValues(new Uint32Array(num)).toString()

interface InputAttrs extends FormControlProps {
  name?: string;
  required?: boolean;
  minLength?: number,
  maxLength?: number,
  pattern?: string,
  min?: number,
  max?: number,
}

interface BoostapFormProps {
  attrs?: FormProps,
  cleandata?: Function
}

interface BoostrapInputProps {
  IsCheckBox?: boolean;
  Floating?: FloatingLabelProps
  InputGroup?: InputGroupProps;
  FormGroup?: FormGroupProps;
  FormControl?: InputAttrs;
  FormCheck?: FormCheckProps;
}

interface FormBuilderElementProps {
  lable: string,
  isCheckbox?: boolean,
  attrs: {
    input?: InputAttrs,
    select?: BoostrapSelectProps
  }


}

interface FormBuilderProps {
  elements: FormBuilderElementProps[],
  form: BoostapFormProps,
  addSubmit?:boolean,
  extra?:JSX.Element
}

interface BoostrapSelectProps extends FormSelectProps {
  lable: string,
  options: React.DetailedHTMLProps<React.OptionHTMLAttributes<HTMLOptionElement>, HTMLOptionElement>[],
}

function BoostrapInput(props: BoostrapInputProps) {
  let input_control = props.IsCheckBox ? <Form.Check {...props.FormCheck} type='checkbox' /> : <Form.Control   {...props.FormControl} />;

  if (props.Floating) {
    return (
      <FloatingLabel controlId={getRandom()}   {...props.Floating}>
        {input_control}
      </FloatingLabel>
    );
  }
  else if (props.FormGroup) {
    return (
      <Form.Group controlId={getRandom()} {...props.FormGroup}>
        {input_control}
      </Form.Group>
    );
  }
  else if (props.InputGroup) {
    return (
      <InputGroup  {...props.InputGroup}>
        {<InputGroup.Text ><i className={ props.InputGroup!.itemProp}></i> </InputGroup.Text>}
        {input_control}
      </InputGroup>
    );
  }
  return (<></>)
}

function BoostrapSelect(props: BoostrapSelectProps) {

  return (<FloatingLabel controlId={getRandom()} label={props.lable} className="mb-3">
    <Form.Select {...props}>
      <option key={getRandom()} value="" >Select menu</option>
      {props.options.map(o => <option key={getRandom()} {...o}> {o.title} </option>)}
    </Form.Select>
  </FloatingLabel>);
}

function BoostrapForm(props: react.PropsWithChildren<BoostapFormProps>) {
  const { handleSubmit, validated, processChanges } = ProcessForm();

  return (
    <Form {...props.attrs} onChange={processChanges} noValidate validated={validated} onSubmit={(e: any) => handleSubmit(e, props.cleandata!)}>
      {props.children}
    </Form>
  )
}

function FormBuilder(props: FormBuilderProps) {

  let _elements = props.elements.map((item: FormBuilderElementProps) => {
    if (item.attrs.input) {
      return (item.isCheckbox ? <BoostrapInput IsCheckBox={true} FormGroup={{ className: "mb-3" }} FormCheck={{ name: item.attrs!.input!.name, label: item.lable, required: item.attrs!.input!.required }} /> : <BoostrapInput Floating={{ label: item.lable, className: "mb-3" }} FormControl={item.attrs!.input} />);
    } else if (item.attrs.select) {
      return <BoostrapSelect {...item.attrs.select!} />;
    } else {
      throw new Error('empty element no input or select data form formBuilder :' + item.lable)
    }
  })
  return (<BoostrapForm {...props.form}> {_elements} {props.addSubmit ? <Button type='submit' variant='primary'> submit </Button> : ""}{props.extra} </BoostrapForm>);
}

//==============================================================================

function ProcessForm() {
  const [validated, setValidated] = useState(false);
 var faildElements: HTMLFormElement[]=[];

  const handleSubmit = (event: any, callback?: Function) => {
    const form: HTMLFormElement = event.currentTarget;

    event.preventDefault();
    event.stopPropagation();

// this check just to refresh state of the form so it can be validated by form.checkValidity() and reset failed elements;
if(faildElements.length){
  console.log(' found faild elements on top: '+faildElements.length)
  faildElements.forEach(async(e:any)=> checkedUp(e,true));
  faildElements = [];
  }

// check form validaty state
    if (!form.checkValidity()) {
      console.log(`form : ${form.id} submited faild validation`)
      // this check is necessary, just to highlight the required feilds, when submit buton clicked without filling all required feilds
      checkFormElements(form);

      return;
    }

    // second inHouse checks after HTML5 validation succeeded
    checkFormElements(form, true, async (result: boolean, elm: HTMLFormElement) => {

      if (!result) {
       faildElements.push(elm)
      console.log(`${elm.name} : ${elm.value} : this element faild checks after form submission ....! : `+faildElements.length)
        
      }
     // elm.classList.remove('is-valid')
    });

    // final check if found form will be returned and brevious step already populated FaildElements
    if(faildElements.length){
    console.log(' number of escaped faild elements: '+faildElements.length)
     return;
    }

    //==== reach final stage all data are clean =============
   // store form values in case form will be returned to user, so values are used to populate the form

    let _clean = Object.fromEntries(new FormData(form).entries()); 
    console.log('form validation succeeded')

    console.log(_clean)
    // set form validation to true
    setValidated(true);
    
    // send data through callback
    callback ? callback(_clean) : console.log(' no clean data callback provided this is the data : ' + _clean);

    //setValidated(true);
    faildElements =[]
    // set form validity to false just before form reset
    setValidated(false);
     form.reset();

     // clean validaty classes
     for (let ee of form.elements){
        ee.classList.remove('is-valid');
        //ee.classList.remove('is-invalid');
     }
    
    return;
  };

  return {
    handleSubmit,
    validated,
    processChanges: ProcessChanges
  }
}

function reg(type: string, _val: any) {
  switch (type) {
    case "postcode":
      return /^((([A-PR-UWYZ][0-9])|([A-PR-UWYZ][0-9][0-9])|([A-PR-UWYZ][A-HK-Y][0-9])|([A-PR-UWYZ][A-HK-Y][0-9][0-9])|([A-PR-UWYZ][0-9][A-HJKSTUW])|([A-PR-UWYZ][A-HK-Y][0-9][ABEHMNPRVWXY]))\s?([0-9][ABD-HJLNP-UW-Z]{2})|(GIR)\s?(0AA))$/.test(_val.toUpperCase()) ? "" : "enter a valid UK PostCode";
    case "mobile":
      let number = _val;
      number = number.replace(/\(|\)|\s+|-/g, "");
      //var test = number.length > 9 && number.match(reg.mobile);
      return number.length > 9 && number.match(/^(?:(?:(?:00\s?|\+)44\s?|0)7(?:[1345789]\d{2}|624)\s?\d{3}\s?\d{3})$/) ? "" : "enter valid UK mobile number";
    case "tel":
      return /^(?:(?:(?:00\s?|\+)44\s?|0)(?:1\d{8,9}|[23]\d{9}|7(?:[1345789]\d{8}|624\d{6})))$/.test(_val) ? "" : "enter valid UK number";
    case "text":
      // /^[a-zA-Z0-9]+$/.test(_val) ? "" : "text and numbers only";
      return /^[A-Za-z0-9 _]*[A-Za-z0-9][A-Za-z0-9 _]*$/.test(_val) ? "" : "text numbers and space only";
    case "textarea":
      return !(/[^A-Za-z0-9 .'?!,@$#-_]/).test(_val) ? "" : "text, numbers & those chars only !,@$#-_ ";
    case "date":
      return /^\d{1,2}\/\d{1,2}\/\d{4}$/.test(_val) ? "" : "this format dd/mm/yyyy";
    case "email":
      return /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(_val) ? "" : "valid email eg: aa@aa.com";
    case "password":
      return /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[$@$!%*?&])[A-Za-z\d$@$!%*?&]{8,10}/.test(_val) ? "" : "8 to 10 chars 1 uppercase lowercase number and special char";
    default: return "";
  }
}
function customChecks(elm: any ) {
  let tagname = elm.tagName.toLowerCase();
  if (tagname === 'input') {
    elm.setCustomValidity(reg(elm.getAttribute('type')!.toLowerCase(), elm.value));
  }
  else if (tagname === 'textarea') {
    elm.setCustomValidity(reg(tagname, elm.value));
  }
  else if (tagname === 'file') {
    elm.setCustomValidity(checkfile(elm));
  }

}
function checkedUp(elm: HTMLInputElement, extra = false) {
  extra && customChecks(elm);

  if (!elm.validity.valid) {
    let nxSibling = elm.nextElementSibling;
    if (nxSibling && nxSibling.className.indexOf("invalid-feedback") > -1) {
      nxSibling!.innerHTML = elm.validationMessage;
    } else {
      elm.insertAdjacentHTML("afterend", "<div class='invalid-feedback'>" + elm.validationMessage + "</div>")
    }

    elm.classList.remove('is-valid');
    elm.classList.add('is-invalid');
    return false;
  } else {

    elm.classList.remove('is-invalid');
    elm.classList.add('is-valid');
    return true;
  }
}

function checkfile(elm: HTMLInputElement, filesNo = 2) {
  let files = elm.files, filength = files!.length, _i = 0, _file, fileType;

  if (!files || filength === 0) {
    return `doesn't contain any files.`;
  }

  if (filength > filesNo) return `( ${filength} ) files selcted ? only ${filesNo} allowed`;

  for (_i; _i < filength; _i++) {
    _file = elm.files![_i];
    fileType = _file.type;

    if (_file.name.length > 50) return `file : ${_file.name} , is too long`;

    else if ("image/jpeg image/png image/jpg".indexOf(fileType) === -1) return fileType + ":not accepted only jpeg png jpg images";

    else if (_file.size / 1024 / 1024 > 2) return _file.name + ": size is exceeds 2 MB";

  }

  return "";
}

function checkFormElements(form: HTMLFormElement, extra = false, callback?: Function) {

  for (let i = 0; i < form.elements.length; i++) {
    let item: any = form.elements[i];
    // check for the submit buton
    if ("input select, textarea".indexOf(item.tagName.toLowerCase()) !== -1)
      callback ? callback(checkedUp(item, extra), item) : checkedUp(item, extra)
  }
}

function ProcessChanges(e: any) {
  e.preventDefault();
  checkedUp(e.target);
}

var repopulated = false;

function FormValidation(e: any) {

  e.preventDefault();
  e.stopPropagation();

  let _form: HTMLFormElement = e.target;
  if (repopulated) {
    checkFormElements(_form, true);
    console.log('form pre-populatation checks are done')
  }

  // entry for first form submission
  if (!_form.checkValidity()) {
    console.log(`$form : ${_form.id} submited is not valid`)
    // this check is necessary, just to highlight the required feilds, when submit buton clicked without filling all required feilds
    checkFormElements(_form);

    return;
  }
  //============================= when form passed checks now second test inhouse checks
  let inHouseCheckFail = false;
  let itemsValues: { name: string, value: any }[] = [] // store form values in case form will be returned to user, so values are used to populate the form

  checkFormElements(_form, true, (result: boolean, elm: any) => {
    if ("input select, textarea".indexOf(elm.tagName.toLowerCase()) !== -1) {
      itemsValues.push({ name: elm.name, value: elm.value })
    }

    if (!result) {
      console.log(`${elm.name} : ${elm.value} : this element faild checks after form submission ....!`)

      inHouseCheckFail = true;
      return;
    }
    elm.classList.remove('is-valid')
  })

  // if inHouseCheckFail will return form back to user
  if (inHouseCheckFail) {
    _form.reset();
    itemsValues.forEach(item => {

      let ee: any = _form.elements.namedItem(item.name);
      if (item.name === 'Terms') {
        ee.checked = item.value;
      } else {
        ee.value = item.value;
      }
    })
    repopulated = true;
    console.log('in house checks failed, form returned to user')
    return false;
  }

  console.log('form submited is valid & in house checks were done, form will be reseted in 3 seconds ')
  setTimeout(() => {
    // reset form and variables
    _form.reset();
    inHouseCheckFail = false;
    repopulated = false;
  }, 3000);
  return
}


export { FormBuilder, uuid, getRandom, BoostrapForm, BoostrapInput, checkedUp, reg, checkfile, ProcessChanges, FormValidation, ProcessForm }