import { Link } from 'react-router-dom';
import { Row, Card, Col, Button,} from 'react-bootstrap';
import { BoostrapForm, BoostrapInput } from '../utils/html5validation';



export default function Login() {

  return (
    <div className="hold-transition login-page ">
      <div className="login-box ">
        <div className="login-logo">
          <Link to="/login" className='nav-link' ><b>Login</b>Page</Link>
        </div>

        <Card>
          <Card.Body className="login-card-body">
            <p className="login-box-msg">Sign in to start your session</p>
            <BoostrapForm  >

             <BoostrapInput InputGroup={{ className: "mb-3" , itemProp:"bi bi-envelope"}} FormControl={{type:"email", required:true}}/>
             <BoostrapInput InputGroup={{ className: "mb-3" , itemProp:"bi bi-lock"}} FormControl={{type:"password", required:true, minLength:6, maxLength:11}}/>
              
              <Row>
                <Col sm={8} >
                  <BoostrapInput IsCheckBox={true} FormGroup={{ className: "mb-3" , controlId:"formBasicCheckbox22"}}  FormCheck={{ type:"checkbox",name:"remember", label:"Remember me", required:true }} />
                </Col>

                <Col sm={4}>
                  <Button variant="primary" type="submit">Sign in </Button>
                </Col>

              </Row>
            </BoostrapForm >
            <div className="social-auth-links text-center mb-3">
              <p>- OR -</p>
              <Link to="/" className="btn d-block btn-primary mb-2">
                <i className="bi bi-facebook"></i> Sign in using Facebook

              </Link>
              <Link to="/" className="btn d-block btn-danger">
                <i className="bi bi-facebook"></i> Sign in using Google+
              </Link>
            </div>

            <p className="mb-1">
              <Link className='nav-link text-center ' to="/">I forgot my password</Link>
            </p>
            <p className="mb-0">
              <Link to="/" className="text-center nav-link">Register a new membership</Link>
            </p>
          </Card.Body>

        </Card>
      </div>
    </div>
  )
}
